	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body,
html {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}
/* General styles */
body {
	font-family: Arial, sans-serif;
	margin: 0;
	padding: 0;
	color: #333;
}

/* Header */
header.header {
	background-color: #d4e7c5;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 2rem;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

header.header h1.header__title {
	font-size: 2rem;
	margin: 0;
}

header.header a.header__menu {
	display: block;
	font-size: 1.5rem;
	cursor: pointer;
	color: #333;
	align-content: center;

	padding-inline: 10px;
	padding-block: 10px;
}
nav.nav {
	display: flex;
	flex-direction: row;
}
nav.nav ul.nav__list {
	display: flex;
	flex-direction: row;
	gap: 20px 20px;
}
nav.nav ul.nav__list li.nav__item {
	list-style-type: none;
}
nav.nav ul.nav__list li.nav__item a {
	text-decoration: none;
	color: #616161;

	align-content: center;
}

nav.nav ul.nav__list li.nav__item a:hover {
	text-decoration: underline;
	color: #000;
}

#drawer.active {
	display: block;
}

/* Hero */
div.hero {
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	background-position: center;
	width: 100%;
	min-height: 400px;

	display: flex;
	filter: drop-shadow(0px 2px 100px #e1f0da);
}

/* Main */
main {
	padding: 2rem;
	background-color: #99bc85;
}
div.content {
	padding: 32px;
}
div.content h1.content__label {
	font-size: 32px;
	font-weight: lighter;
	text-align: center;
}
.card-list {
	margin-block: 1rem;

	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));

	column-gap: 2%;
	row-gap: 24px;
}

/* Footer */
footer {
	background-color: #f5f5f5;
	text-align: center;
	padding: 1rem;
}

/*dimensi aksesibilitas  */
a,
input,
button,
textarea {
	display: inline-block;
	min-width: 44px;
	min-height: 44px;
}
